import Vue from 'vue'
import VueResource from 'vue-resource'
import VueRouter from 'vue-router'
import ExperimentsDashboard from './dashboards/ExperimentDashboard.vue'
import NewApplication from './components/admin/NewApplication.vue'
import AdminDashboard from './dashboards/AdminDashboard.vue'

Vue.config.productionTip = false

Vue.use(VueResource)
Vue.use(VueRouter)

const routes=[
  {path:'/vue/admin/main',component:ExperimentsDashboard},
  {path:'/vue/admin/application',component:NewApplication}
]

export function initializeApacheAiravataDashboard(dashboardName) {
  return new Vue({
    el: '#app',
    template: '<' + dashboardName + '/>',
    components: {ExperimentsDashboard, NewApplication, AdminDashboard}

  })
};





